.container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 1rem;
    & > div {
        height: 100px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }
    & > div > h2 {
        color: #fff;
        text-align: center;
        font-size: 24px;
    }
}
